import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { TeamManageBean } from '../bean/tean-manage.bean';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
// import { OrgStatus, OrgTypes } from '../../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'team-update',
    templateUrl: 'team-update.html',
    styleUrls: ['team-update.scss'],
    providers: [Commfunc]
})

export class TeamUpdate implements OnInit {

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    //实体
    teamManageBean: TeamManageBean = new TeamManageBean();
    userform: FormGroup; //校验

    //机构状态下拉引入
    orgStatus: any = [];
    orgType: any = [];
    //上级机构模糊查询接收需要的显示数据
    list: any;
    list1: any;
    superiorManageName: string;//上级机构名称
    superiorManageOrg: string;//上级机构编号
    inpFlag: boolean = true;
    code: any;
    constructor(
        public commonHttpService: CommonHttpService,
        public commfunc: Commfunc,
        public fb: FormBuilder,
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.orgStatus = this.code['OrgStatus'];
        this.orgType = this.code['OrgTypes'];
    }

    ngOnInit() {
        this.orgquery('', '');
        this.userform = this.fb.group({
            'orgId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
            'orgName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
            'orgType': new FormControl('', Validators.required),
            // 'operatingLevel': new FormControl('', Validators.required),
            'orgStatus': new FormControl('', Validators.required),
            'superiorManageOrg': new FormControl('', Validators.required),
            'superiorManageName': new FormControl(''),

        });
    }
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    ngOnChanges() {
        this.fuzhi();
    }
    fuzhi() {
        this.teamManageBean = this.inValue;
        this.superiorManageOrg = this.inValue.superiorManageOrg
    }
    //机构模糊查询
    inpquery($event) {
        if (this.inpFlag) {
            this.inpFlag = false;
            setTimeout(() => {
                this.inpFlag = true;
                let orgUptId = document.getElementById('orgUptId').getElementsByTagName('input')[1].value
                let orgUptName = document.getElementById('orgUptName').getElementsByTagName('input')[1].value
                this.orgquery(orgUptId, orgUptName)
                // this.orgquery($event.path[0].value)
            }, 1000)
        }
    }
    orgquery(orgId, orgName) {
        let param = {
            orgId: orgId,
            orgName: orgName,
            pageSize: 30,
            OrgStatus: '0'
        }
        this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
            if (data.orgList) {
                this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
                // this.teamManageBean.rowNum = data.total
                this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
            }
        })
    }

    queryOrgId() {

        let param = {
            pageSize: 30,
            pageNum: 1,
            OrgStatus: '0'
        }
        this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
            if (data.orgList) {
                this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
                // this.teamManageBean.rowNum = data.total
                this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
            }
        })
    }
    //onchange事件
    doChose(event) {
        this.superiorManageName = this.returnValue(event.value, 'label', 'value');
        // this.list.value = this.list1.label
    }
    doChose1(event) {
        this.superiorManageOrg = this.returnValue(event.value, 'value', 'label');
    }
    /**
     * 转换值
     * @param param  值
     * @param colum  字段
     */
    returnValue(param, colum, valueColum) {
        let value: string = "";
        this.list.forEach(element => {
            if (element[colum] === param) {
                value = element[valueColum];
            }
        });
        return value;
    }
    //提交
    preserveClick() {
        //校验的

        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.teamManageBean.operatingLevel = 'TEAM'
        if (this.superiorManageName) {
            this.teamManageBean.superiorManageOrg = this.superiorManageName;
        } else {
            this.teamManageBean.superiorManageOrg = this.superiorManageOrg
        }
        this.commonHttpService.modifyOrg(this.teamManageBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });

    }
    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
    }
}