import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { SharedBasisModule } from '../../../../../../../app/sharedBasis.module';
import { TeamManageBean } from '../bean/tean-manage.bean';
import { Message } from 'primeng/primeng';
import { CommonHttpService } from '../../../http/common.http.service';
// import { OrgStatus, OrgTypes } from '../../../constant/common.codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun'

@Component({
  selector: 'team-add',
  templateUrl: 'team-add.html',
  styleUrls: ['team-add.scss'],
  providers: [CommonHttpService, Commfunc]
})

export class TeamAdd implements OnInit {

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  userform: FormGroup; //校验
  //实体

  //提示信息
  msgs: Message[] = [];
  //  add_title: string = '产品线管理新增';
  //机构状态下拉引入
  orgStatus: any = [];
  orgType: any = [];
  //查询机构模糊查询
  list: any;
  list1: any;
  teamManageBean: TeamManageBean = new TeamManageBean();
  superiorManageOrg;
  superiorManageName;
  inpFlag: boolean = true;
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.orgStatus = this.code['OrgStatus'];
    this.orgType = this.code['OrgTypes'];
  }

  ngOnInit() {
    this.teamManageBean.operatingLevel = '0'
    this.userform = this.fb.group({
      'orgId': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
      'orgName': new FormControl('', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/), Validators.required]),
      'orgType': new FormControl('', Validators.required),
      // 'operatingLevel': new FormControl('', Validators.required),
      'orgStatus': new FormControl('', Validators.required),
      'superiorManageOrg': new FormControl('', Validators.required),
      'superiorManageName': new FormControl('', Validators.required)
    });
    // this.queryOrgId()
    this.orgquery('', '')
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  inpquery(event) {
    if (this.inpFlag) {
      this.inpFlag = false;
      setTimeout(() => {
        this.inpFlag = true;
        let orgAddId = document.getElementById('orgAddId').getElementsByTagName('input')[1].value
        let orgAddName = document.getElementById('orgAddName').getElementsByTagName('input')[1].value
        this.orgquery(orgAddId, orgAddName)
        // this.orgquery($event.path[0].value)
      }, 1000)
    }
  }
  //机构模糊查询
  orgquery(orgId, orgName) {
    let param = {
      orgId: orgId,
      orgName: orgName,
      pageSize: 30,
      OrgStatus: '0'
    }
    this.commonHttpService.queryVagueOrgList(param).subscribe(data => {
      if (data.orgList) {
        this.list = this.commfunc.listToLabelvalue(data.orgList, 'orgName', 'orgId');
        // this.teamManageBean.rowNum = data.total
        this.list1 = this.commfunc.listToLabelvalue(data.orgList, 'orgId', 'orgName');
      }
    })
  }

  // 保存 增加确定
  preserveClick() {
    //校验的

    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //提交服务
    this.teamManageBean.operatingLevel = 'TEAM';
    this.teamManageBean.superiorManageOrg = this.superiorManageName;
    this.teamManageBean.superiorManageName = this.superiorManageOrg;
    this.commonHttpService.createOrg(this.teamManageBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.postsManageBean = new PostsManageBean();
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  closeBtn() {
    this.outValue.emit(false);
  }

  //onchange事件
  doChose(event) {
    this.superiorManageName = this.returnValue(event.value, 'label', 'value');
    // this.list.value = this.list1.label
  }
  doChose1(event) {
    this.superiorManageOrg = this.returnValue(event.value, 'value', 'label');
  }
  /**
   * 转换值
   * @param param  值
   * @param colum  字段
   */
  returnValue(param, colum, valueColum) {
    let value: string = "";
    this.list.forEach(element => {
      if (element[colum] === param) {
        value = element[valueColum];
      }
    });
    return value;
  }
}