import { Component, OnInit, Input } from '@angular/core';
import { TeamManageBean } from './bean/tean-manage.bean';
import { CommonHttpService } from '../../http/common.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
// import { OrgTypes, OrgStatus, OperatingLevel, LegalFlag } from '../../constant/common.codeValue';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { specialSymbol } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'team-manage',
    styleUrls: ['team-manage.scss'],
    templateUrl: 'team-manage.html',
    providers: [ConfirmationService, Commfunc]
})

export class TeamManage implements OnInit {
    // @Input() inValue: any;
    precredit: string = ' 营销团队管理';
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    // 表体内容
    tableData: any;
    teamManageBean: TeamManageBean = new TeamManageBean()


    //总页数
    total: string;
    //每页多少条
    rowNum: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    //模态框是否显示
    display: boolean = false;

    showModel: any;

    //传入的值
    inValue: string;

    //修改的参数
    updateValue: any = [];

    treeOrgName: string;

    // orgType: any[] = OrgTypes;
    orgStatus: any[] = [];
    // operatingLevel: any[] = OperatingLevel;
    // legalFlag: any[] = LegalFlag;
    //校验
    userform: FormGroup;
    first: number = 0;
    code:any;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public fb: FormBuilder
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.orgStatus = this.code['OrgStatus'];
    }
    ngOnInit() {
        this.userform = this.fb.group({
            'superiorManageOrg': new FormControl('', Validators.compose([specialSymbol])),
            'superiorManageOrgName': new FormControl('', Validators.compose([specialSymbol])),
            'orgId': new FormControl('', Validators.compose([specialSymbol])),
            'orgName': new FormControl('', Validators.compose([specialSymbol]))
        })
        this.teamManageBean.rowNum = '10';
        this.teamManageBean.startRow = '1';
        this.teamManageBean.operatingLevel = 'team';
        this.teamManageBean.orgStatus = '0';
        this.query();

        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.queryClick();
            }
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    ngOnChanges() {
        this.ngOnInit();
    }
    //查询
    query() {
        //校验
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //校验是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.teamManageBean.flag = '0';//后台服务需要模糊查询标识
        this.teamManageBean.operatingLevel = 'TEAM';//后台服务需要机构级别默认
        // this.teamManageBean.orgStatus='0';//机构状态默认
        this.commonHttpService.queryOrgByCombConditions(this.teamManageBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.code });
                this.teamManageBean.startRow = '1';
                if (data.orgInfoList.orgStatus == '1') {
                    var stamp = document.getElementById('displayButton');
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: data.returnCode.code });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    queryClick() {
        this.first = 0;
        this.teamManageBean.pageNum = '1';
        this.teamManageBean.flag = '0';//后台服务需要模糊查询标识
        this.teamManageBean.operatingLevel = 'TEAM';//后台服务需要机构级别默认
        // this.teamManageBean.orgStatus='0';//机构状态默认
        this.commonHttpService.queryOrgByCombConditions(this.teamManageBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.tableData = data.orgInfoList;
                this.total = data.rowCount;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.code });
                this.teamManageBean.startRow = '1';
                if (data.orgInfoList.orgStatus == '1') {
                    var stamp = document.getElementById('displayButton');
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'info', summary: '提示', detail: data.returnCode.code });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.teamManageBean.rowNum = event.rows;
        //当前页
        let currentPage = event.page + 1 + '';
        this.teamManageBean.startRow = currentPage;
        this.first = event.rows * event.page;
        //调用查询的方法
        this.query();
        // this.teamManageBean.startRow = '1';
    }

    //修改
    update(item) {
        this.headerTitle = '修改营销团队';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        // this.teamManageBean = new TeamManageBean();
        // this.teamManageBean.rowNum = "10";
        // this.teamManageBean.startRow = "1";
        this.teamManageBean.orgStatus = '0';
        this.query();
    }

    //删除

    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                let param = { orgId: item.orgId, orgStatus: '1' };
                this.commonHttpService.maintainOrgStatus(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }
    //添加
    add() {
        this.headerTitle = '创建营销团队';
        this.display = true;
        this.showModel = 1;
    }

    //添加的回调函数
    addCall(param) {
        this.display = param;
        // this.teamManageBean.startRow = '1';
        this.teamManageBean.orgStatus = '0'
        this.query();
    }


    // 重置
    reset() {
        this.teamManageBean.orgId = '';
        this.teamManageBean.orgName = '';
        this.teamManageBean.superiorManageOrg = '';
        this.teamManageBean.superiorManageOrgName = '';
        this.teamManageBean.orgStatus = '';
    }


    //返回的值
    outValue(param) {
        this.display = param.display;
    }
    product() {
        this.display = true;
        this.showModel = '5';
    }
    orgTree() {
        this.headerTitle = '机构树';
        this.display = true;
        this.showModel = '6';
    }
    treeCall(param) {
        this.display = false;
        this.treeOrgName = param.orgName;
    }
    aa() {
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}